.theme-box{
  border-radius: 10px;

}
.theme-block1{
  background: linear-gradient(135deg, #1cefff, rgb(44, 56, 221) 60%);
}

.theme-block2{
  background: linear-gradient(135deg, #eea849, #f46b45 50%);
}

.theme-block3{
  background: linear-gradient(135deg, rgb(159, 206, 83), #03722e 50%);
}

.theme-block4{
  background: linear-gradient(135deg, rgb(255, 216, 110), rgb(185, 124, 10) 50%);
  
}
.theme-block5{
  background: linear-gradient(135deg, #FFC11F, #F33C04 40%,#DA1F05 50% );
  
}
.theme-block6{
  background: linear-gradient(135deg, #FFF75D, #FFC11F 50%);
  
}
.clip-class-theme{
  background-color: rgba(34, 30, 30);
  clip-path: polygon(0 0,100% 0, 100% 100%, 20% 100%);
}
@media only screen and (max-width:900px){
  .clip-class-theme{
    clip-path: none;
  }
}